import { backgroundColor, primaryColor } from "assets/jss/common/threemysticapes.js"

export const globalPageStyle = {
    "@global":{
        ".ReactModalPortal":{
            position:"relative",
            zIndex: 9000,
        },
        ".postListGrid": {
            "&.dark": {
                color: primaryColor.normal.dark.textColor
            },
            "& > .MuiGridListTile-tile": {
                "& > a:first-of-type": {
                    "& > img:first-of-type": {
                        height: "100%",

                    }
                }
            },
            "& .MuiGridListTileBar-titleWrap": {
                "& h3,h6": {
                    marginTop: "0rem",
                    marginBottom: "0rem",
                    whiteSpace: "break-spaces",
                }

            }
        },
    },
    "htmlRoot": {
        height: "100%",
        "&.dark": {
            "background-color": backgroundColor.dark.color
        }

    },
    "page": {
        height: "100%",
        "&.dark": {
            "background-color": backgroundColor.dark.color,
            color: backgroundColor.dark.textColor,
            "& a":{
                color: backgroundColor.dark.textColor
            }
        },
        "& > div:first-of-type": {
            height: "100%",
            "& > div:first-of-type": {
                height: "100%",
            }
        }
    },
    "parallaxPage": {
        height: "30vh",
        maxHeight: "300px",
    },
    "sectionBody": {
    },
    mainContent: {
        minHeight: "65vh",
        padding: "30px 0",
        "&.dark":{
            color: backgroundColor.dark.textColor
        }
    }

};

export default {
    globalPageStyle,
}