import React from 'react';
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import { Link } from "gatsby"
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Typography from '@material-ui/core/Typography';

import breadCrumbStyle from "assets/jss/components/layout/section/page/breadCrumbs.js";

const useStyleBreadCrumb = makeStyles(breadCrumbStyle);

PageBreadcrumb.propTypes = {
    props: PropTypes.object,
}

function GetNodeSlug({ pageNode, location }) {
    if (location === null || typeof location === 'undefined') {
        return pageNode.slug;
    }

    if (location.state === null || typeof location.state === 'undefined') {
        return pageNode.slug;
    }

    if (location.state.crum === null || typeof location.state.crum === 'undefined') {
        return pageNode.slug;
    }

    return location.state.crum.pageNode.slug;
}

function GetTag({ pageContext, location }) {
    if (location === null || typeof location === 'undefined') {
        return pageContext.tag;
    }

    if (location.state === null || typeof location.state === 'undefined') {
        return pageContext.tag;
    }

    if (location.state.crum === null || typeof location.state.crum === 'undefined') {
        return pageContext.tag;
    }

    if (location.state.crum.postGroupValue === null || typeof location.state.crum.postGroupValue === 'undefined') {
        return pageContext.tag;
    }

    return location.state.crum.postGroupValue;
}

function GetLocationPathName(location){
    if (location === null || typeof location === 'undefined') {
        return null;
    }
    if (location.pathname === null || typeof location.pathname === 'undefined') {
        return null;
    }

    return location.pathname;
}

function GetCrumb({ pageNode, pageContext, location }) {
    const nodeSlug = GetNodeSlug({ pageNode, location});
    const locationSlugRaw = GetLocationPathName(location);
    if (locationSlugRaw === null){
        return null;
    }
    const locationSlug = locationSlugRaw.trimRight("/,\\");
    const returnChildren = []
    if (locationSlug.startsWith("/blog/")) {
        returnChildren.push(<Link 
            key={`/blog`}
            to={`/blog`}
            title={`Blog`}
            aria-label={`Blog`}
        >Blog</Link>)
    }
    if (nodeSlug === "/blog/tag" ) {
        const tag = GetTag({ pageContext, location })
        if ((tag === null || typeof tag === 'undefined')) {
            return returnChildren;
        }
        returnChildren.push(<Link
            key={`/blog/tag`}
            to={`/blog/tag`}
            title={`Post by Tag`}
            aria-label={`Post by Tag`}
        >Post by Tag</Link>)
        return returnChildren;
    }
}

function GetCrumbTitle({pageNode, pageContext, location}) {
    const nodeSlug = pageNode;
    if (nodeSlug === "/blog/tag") {
        if ((pageContext.tag !== null && typeof pageContext.tag !== 'undefined')){
            return pageContext.tag;
        }
        return pageNode.context.title;
    }

    return pageNode.context.title;
}
//{GetCrumbTitle(pageNode, pageContext)}
export default function PageBreadcrumb({ pageNode, location, pageContext, ...props }) {
    const breadCumbClasses = useStyleBreadCrumb();
    const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";  
    if (pageNode.slug === "/"){
        return null;
    }

    const breadCumbClassName = classNames({
        [breadCumbClasses.breadcrumb]:true,
        [themeVariation]: true,
    });

    return (
        <Breadcrumbs className={breadCumbClassName} aria-label="breadcrumb">
            <Link
                to={`/`}
                title={`Home`}
                aria-label={`Home`}
            >Home</Link>
            {GetCrumb({ pageNode, pageContext, location })}
            <Typography color="textPrimary">{GetCrumbTitle({pageNode, location, pageContext})}</Typography>
        </Breadcrumbs>
    );
}